<template>
    <v-container fluid>     
        <v-toolbar >
            <v-toolbar-title>사용자 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>       
            <v-spacer></v-spacer>
            <v-btn color="primary" dark class="mr-2" @click="openDialog()">사용자 추가</v-btn>
        </v-toolbar>
        <v-card>
            <v-card-title>                
                <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details sm="2"></v-text-field>                
            </v-card-title>
            <!--
            <v-data-table :headers="headers" :items="users" :search="search" :loading="loading" @dblclick:row="showRowInfo" />
            -->
            <v-data-table :headers="headers" :items="users" :search="search" :loading="loading" @dblclick:row="showRowInfo" >            
                <template v-slot:item="{ item }"  >
                    <tr @dblclick="handleClick(item)">
                        <td width="160"> <v-select v-model="item.i_com" :readonly="true" :items="compnayLists" item-text="n_com" item-value="i_com"></v-select> </td>
                        <td> {{item.i_id}} </td>
                        <td> {{item.n_name}} </td>
                        <td> {{item.i_password}} </td>
                        <td> {{item.t_memo}}</td>                        
                    </tr>
                </template>            
            </v-data-table>
        </v-card>
        
    
        <ez-dialog label="사용자 추가/수정" ref="dialog" width="400" color="primary" persistent>
            <sys-users-form :user="user" :companys="compnayLists" @onSave="save">

            </sys-users-form>
        </ez-dialog>
    </v-container>
</template>

<script>
import { mapActions } from "vuex";
import EzDialog from '../../components/etc/EzDialog.vue';
import SysUsersForm from './SysUsersForm.vue';


export default {
  components: { EzDialog, SysUsersForm },
    name: "SysUsers",
    data() {
        return {
            search: '',
            loading: false,
            compnayLists: [],

            headers: [
                { text: '사업장코드', align: 'start', filterable: false, value: 'i_com', width: "100px", fixed: true},
                { text: 'ID', value: 'i_id' },
                { text: '성명', value: 'n_name' },
                { text: '비밀번호', value: 'i_password' },                
                { text: '메모', value: 't_memo'},
            ],

            users: [],
            company: -1,
            curUsers: [],
            user: null,
        };
    },
    computed: {
        companyUsers() {           
        },
        companyName() {           
        },
    },
    watch: {
        company() {
            
        }
    },
    mounted() {
        this.fetchData();
    },
    methods: {        
        ...mapActions("company", ['createUser']),
        
        async fetchData() {            
            this.compnayLists = await this.$axios.get("/api/company/companylist");
            this.users = await this.$axios.get("/api/company/companyusers");                 
        },
        openDialog(item) {
            this.user = null;
            this.$refs.dialog.open();
        },
        showRowInfo(event, { item }) {
            this.user = item;
            this.$refs.dialog.open();
        },       
        async save(form) {
            this.isLoading = true;
            const data = await this.createUser(form);
            this.isLoading = false;
        }
    },
}
</script>

